import React, { useEffect, useState } from 'react';
import { __ } from '@wordpress/i18n';
import {
	sendPostMessage,
	getColorScheme,
	generatePalleteByColor,
} from '../../../../utils/functions';

const BrandColorPallete = ( { templateResponse, colorScheme } ) => {
	const [ brandColor, setBrandColor ] = useState( '' );

	const handleChange = ( event ) => {
		setBrandColor( event.target.value );
	};

	useEffect( () => {
		const templateType = getColorScheme( templateResponse );
		const sampleColors = [ ...colorScheme[ 0 ].colors ];
		const generatedColors = generatePalleteByColor(
			brandColor,
			templateType,
			sampleColors
		);
		if ( generatedColors.length ) {
			generatedColors.map( ( value, i ) => {
				if ( i < 5 ) {
					const previewItem = document.getElementsByClassName(
						`brand-color-${ i + 1 }`
					)[ 0 ];
					previewItem.style.backgroundColor = value;
				}
			} );

			const palette = {
				slug: 'brand-pallete',
				title: 'Brand Palette',
				colors: generatedColors,
			};

			sendPostMessage( {
				param: 'colorPalette',
				data: palette,
			} );
		}
	}, [ brandColor ] );
	return (
		<>
			<h4>
				{ __( 'Generate Palette using Brand Color?', 'astra-sites' ) }
			</h4>
			<div className="brand-color-palette-wrap ist-color-palette">
				<input
					type="color"
					onChange={ handleChange }
					colorpick-eyedropper-active="false"
				/>
				<div className="brand-palette-preview ist-colors-list">
					<div className="ist-palette-color brand-color-1" />
					<div className="ist-palette-color brand-color-2" />
					<div className="ist-palette-color brand-color-3" />
					<div className="ist-palette-color brand-color-4" />
					<div className="ist-palette-color brand-color-5" />
				</div>
			</div>
		</>
	);
};

export default BrandColorPallete;
